import Layout from '../components/Layout';
import Link from 'next/link';

const PostLink = ({title}) => (
  <li>
    <Link as='/react-post' href={`/post?title=${title}`}>
      <a>{title} </a>
    </Link>
  </li>
)

export default () => (
  <Layout title='Blog'>
    <ul>
      <PostLink slug='' title='React Post'/>
      <PostLink title='Angular Post'/>
      <PostLink title='Flutter Post'/>
      <PostLink title='Vue Post'/>
    </ul>
  </Layout>
);
